
.container,
.swipe-wrap,
.swipe-item {
  width: 100%;
  height: 100%;
}
.swipe-item h1 {
  margin-top: 50px;
}
.scroll-wrap {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.scroll {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  width: auto;
  height: auto;
  -webkit-overflow-scrolling: touch;
  background: #eee;
  text-align: center;
}



